<template>
    <div class="contains">
        <!-- <iframe allow="autoplay" :src="music.url" style="display: none;"></iframe> -->
        <button @click="playMusic" style="display: none;">播放</button>
        <audio ref="audio" loop="loop" autoplay="autoplay" :src="music.url" controls="controls" style="display: none;"></audio>
        <div :class="`images img${index}`" v-for="(item,index) in imageList"><img :src="item.url"></div>
       <!-- <div class="images img0"><img :src="imageList[0].url"></div>
        <div class="images img1"><img :src="imageList[1].url"></div>
        <div class="images img2"><img :src="imageList[2].url"></div> -->
<!--        <div class="images img4"><img src="require('@/images/004.jpg')"></div>
        <div class="images img5"><img src="require('@/images/005.jpg')"></div> -->
       <!-- <div class="images img6"><img src="images/006.jpg"></div>
        <div class="images img7"><img src="images/007.jpg"></div> -->
    </div>
</template>

<script>
  export default {
    mounted(){
     let button=document.querySelector("button")
     button.click();

    },
    data() {
      return{
          imageList:[
            {url:require('@/assets/images/000.jpg')},
            {url:require('@/assets/images/001.jpg')},
            {url:require('@/assets/images/002.jpg')},
            {url:require('@/assets/images/003.jpg')},
            {url:require('@/assets/images/004.jpg')},
            {url:require('@/assets/images/005.jpg')},
          ],
          music:{
            url:require('@/assets/music/1.mp3')
          }
      }
    },
    methods:{
        playMusic(){
          this.$refs.audio.play()
        }
    }
  }
</script>

<style scoped>

 .contains{
 /*滚动容器*/
     width:100%;
     position: relative;
     transform-style: preserve-3d;
     height:100vh;
     transform: rotate3d(45deg);
     animation:rotate3D 20s linear infinite;
 }

 .images{
     width:40vw;
     height:100vh;
     /*transform:translateY(200px);*/
     position: absolute;
     top: 0;
     bottom: 0;
     left: 0;
     right: 0;
     margin: auto;
 }
 .contains img{
     width:40vw;
     height:100vh;
 }

 .img0{
     transform: rotateY(0deg) translateX(400px) translateZ(300px) ;
     /*-webkit-animation:rotate3D_0 10s linear infinite;*/

 }
 .img1{
     transform:  rotateY(60deg) translateX(400px) translateZ(300px);
     /*-webkit-animation:rotate3D_1 10s linear infinite;*/
 }
 .img2{
     transform:  rotateY(120deg) translateX(400px) translateZ(300px);
     /*-webkit-animation:rotate3D_2 10s linear infinite;*/
 }
 .img3{
     transform:  rotateY(180deg) translateX(400px) translateZ(300px);
     /*-webkit-animation:rotate3D_3 10s linear infinite;*/
 }
 .img4{
     transform: rotateY(240deg) translateX(400px) translateZ(300px);
     /*-webkit-animation:rotate3D_4 10s linear infinite;*/
 }
 .img5{
     transform: rotateY(300deg) translateX(400px)translateZ(300px);
     /*-webkit-animation:rotate3D_5 10s linear infinite;*/
 }
 .img6{
     transform: rotateY(0deg);
     /*-webkit-animation:rotate3D_6 10s linear infinite;*/
 }

 @keyframes rotate3D{
     0%{
         transform: rotateY(0deg);
     }
     16.7%{
         transform:rotateY(60deg);
     }
     33.3%{
         transform:rotateY(120deg);
     }
     50.1%{
         transform: rotateY(180deg);
     }
     66.7%{
         transform: rotateY(240deg);
     }
     83.4%{
         transform: rotateY(300deg);
     }
     100%{
         transform:rotateY(360deg);
     }

 }

</style>



